@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;1,400&family=Poppins:wght@100;200;300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Zen+Antique&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper{
    margin-top: 20px;
    width: 90%;
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(20rem, 12rem));
    grid-gap: 1rem;
    gap: 1rem;
    margin-left: 120px;
    margin-bottom: 50px;
   
   
}
.box-card{
    width: 100%;
    height: 180px;

    overflow: hidden;
    border: none;
    padding: 15px 10px 10px 10px; 
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px; 
}
.box-card-body h4{
    color: #FF0000;
    font-weight: 700;
}
.box-card-body{
    display: flex;
    flex-direction: column;
}
.inner-card{
    display: flex;
    margin-top: 10px;
}
.inner-card h4{
    font-size: 40px;
    color: #333;
    margin-right: 20px;
}
.deep-card p{
    color: gray;
}
.deep-card a{
    text-decoration: none;
    color: #fff;
    background: #FF0000;
    padding: 10px 20px;
    font-weight: 700;
}
.deep-card a:hover{
    color: #fff;
    background: #333;
}

.image{
    position: relative;
    height: 100vh;
}
.content{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    height: 100vh;
    width: 100%;
}



.top-nav{
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 100px;
}
.top-nav a{
    text-decoration: none;
    color: gray;
    padding: 0px 20px;
    cursor: pointer;
}
.top-nav a:hover{
    color: #f5f5f5f5;
    background: red;
    padding: 10px;
    border-radius: 5px;
    transition: 0.5s;
    -webkit-transform: translateX(0.5s);
            transform: translateX(0.5s);
}
.banner{ 
    background: url(/dbms-bank/static/media/banner-img-4.62307219.jpg);
    background-repeat: no-repeat;
    height: 600px;
    margin-left: 100px;
}


.getstarted{
    margin-top: 30px;
    float:left;
    width: 800px;
    height: 400px;  
}
.getstarted h1{
    color: #333;
    font-size: 70px;
    font-weight: 700;
    font-family: 'Zen Antique', serif;
}
.getstarted p{
    color: lightgray;
    margin-left: 100px;
    text-align: justify;
}
.getstarted h3{
    color: #fff;
    font-size: 24px;
    background: red;
    padding: 10px;
    width: 200px;
    text-align: center;
    font-weight: 700;
    border-radius: 4px;
    -webkit-clip-path: polygon(12% 0, 100% 0, 88% 100%, 0 100%);
            clip-path: polygon(12% 0, 100% 0, 88% 100%, 0 100%);
}

.services{
    margin-top: 50px;
    text-align: left;
    margin-left: 90px;
    margin-bottom: 20px;
    width: 600px;
}
.services h2{
    font-size: 35px;
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1PX solid #E6E6E6;
}
.services h2 span{
    margin-left: 25px;
    font-size: 24px;
}
.search-box{
    position: absolute;
    bottom: 50%;
    left: 30%;
    background: #fff;
    padding-left: 30px;
}

.bottom-sec{
    width: 580px;
    height: 70px;
    justify-content: center;
    align-items: center;
    display: flex;
    
}
.bottom-sec a{
    text-decoration: none;
    font-size: 18px;
    background: #C70039;
    padding: 10px 18px;
    margin-right: 55px;
    border-radius: 8px;
}
.bottom-sec a:hover{
    background: #ffb919;
   
}
.bottom-sec a span{
    color: #fff;
    margin-left: 5px;
}
.bottom-sec a span:hover{
    color: #fff;
} 
.list{
    list-style: none;
    font-size: 14px;
    display: flex;
    padding: 10px;
    background: #C70039;
    color: #fff;
    border-right: 1px solid #fff;
    font-family: 'Roboto', sans-serif;
    float: right;
    cursor: pointer;
    transition: 0.5s;
   justify-content: center;
}
.list:hover{
    color: #333;
    background-color: #FFB919;
    
}
.list-item{

    height: 50px;
    padding: 30px 100px;
    
}
.top-contact{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    width: 100%;
    
}
small{
    margin-left: 10px;
    margin-right: 10px;
    cursor: pointer;
}
